import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom';
import { addReview, loadReviews } from '../store/actions/reviewActions';
export class _AddReview extends Component {

    state = {
        reviewToEdit: {
            txt: '',
            toyId: ''
        }
    };

    async componentDidMount() {
        await this.props.loadReviews(this.props.toyId);
        console.log(this.props.reviews,'GOT REVIEWS');
        
    }
    handleChange = ev => {
        const { name, value } = ev.target;
        this.setState(prevState => ({
            reviewToEdit: {
                ...prevState.reviewToEdit,
                [name]: value
            }
        }));
    };

    addReview = ev => {
        ev.preventDefault();
        this.props.addReview(this.state.reviewToEdit);
        this.setState({ reviewToEdit: { txt: '', toyId: '' } });
    };


    render() {
        return (
            <div>
        {this.props.reviews && <ul>
          {this.props.reviews.map(review => 
              (
            <li key={review._id}>
              <h3>New Review: {review.txt}</h3>
              <p>
                <Link to={`user/${review.toyId}`}>
                  About - 
                   {review.aboutToy.name}
                </Link>
              </p>
              <p>
                <Link to={`user/${review.byUserId}`}>
                  By -
                  {review.byUser.username}
                </Link>
              </p>
              <hr />
            </li>
    ))}
        </ul>}

                {/* ADD REVIEW SECTION */}
                {this.props.loggedInUser&&<h2>Add Review:</h2>}
                {this.props.loggedInUser &&
                    <form onSubmit={this.addReview}>
                        <select
                            onChange={this.handleChange}
                            value={this.state.reviewToEdit.toyId}
                            name="toyId"
                        >
                            <option value="">Select Toy</option>
                            {this.props.toys.map(toy => (
                                <option key={toy._id} value={toy._id}>
                                    {toy.name}
                                </option>
                            ))}
                        </select>
                        <textarea
                            name="txt"
                            onChange={this.handleChange}
                            value={this.state.reviewToEdit.txt}
                        ></textarea>
                        <button>Submit</button>
                    </form>}
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        toys: state.toyReducer.toys,
        loggedInUser: state.userReducer.loggedinUser,
        reviews:state.reviewReducer.reviews

    }
}
const mapDispatchToProps = {
    addReview,
    loadReviews

}


export const AddReview = connect(mapStateToProps, mapDispatchToProps)(_AddReview)
